@import '../../scss/config';
.m-checkSms{
    .u-title{
        display: block;
        font-size: .4rem;
        text-align: center;
        padding: .6rem 0;
    }
    .u-tips{
        color:$fc_tips;
        font-size: .24rem;
        padding-top:.1rem;
        padding-bottom:.1rem;
        p {
            line-height: .4rem;
        }
    }
    .u-clauses{
        margin:.75rem 0 -0.75rem 0;
        .u-tips{
            position: relative;
            padding-left:.45rem;
            .i-checkbox{
                display: block;
                position: absolute;
                width: .3rem;
                height: .3rem;
                left: 0;
                top:.15rem;
                border: solid 1px $bd_blue;
                border-radius: 2px;
                &.checked{
                    background-color: $bd_blue;
                    text-align: center;
                    line-height: .3rem;
                }
            }
        }
    }
    .ant-smsup-show{
        -webkit-animation: smsUpShowKF .6s ease-out;
        animation: smsUpShowKF .6s ease-out;
    }
    /* æ¸å¥å¨ç» */
    @keyframes smsUpShowKF{
        0%{opacity:0.2; transform: scale(1.2,1.2) }
        100%{opacity:1;}
    }
    @-webkit-keyframes smsUpShowKF{
        0%{opacity:0.2; -webkit-transform: scale(1.2,1.2) }
        100%{opacity:1;}
    }

    .m-capPop{
        .m-pop {
            width: 85%;
            max-width: 8.5rem;
            .pop-info {
                padding: 15px 10px;
                .ncpt_widget{
                    display: block;
                }
                &.m-ipt{
                    background: none;
                    border: none;
                    margin: 0;
                }
                &.pop-cap{
                    padding: 30px 10px 90px;
                    .m-captcha{
                        .u-ipt{
                            height: auto;
                            padding-right: 2.3rem;
                            .ipt_wrap {
                                border: solid 1px #D0D0D0;
                                padding: 0px .65rem 0 .2rem;;
                                border-radius: 2px;
                                input {
                                    height: .8rem;
                                }
                            }
                            .ipt_extend{
                                top: .21rem;
                                right: 0;
                            }
                        }
                        .ipt_tip{
                            display: none;
                        }
                        .u-img{
                            right: 0;
                            top: 0;
                            border: solid 1px #fff;
                            font-size: .28rem;
                        }
                    }
                }
            }
            .pop-btn .u-btn{
                height: 100%;
                width: 100%;
                &>button{
                    height: 100%;
                    width: 100%;
                    background: none;
                    border: none;
                    color: #666;
                }
                .btn_mask{
                    opacity: .3;
                }
            }
        }
    }

}



