body {
    .u-btn button {
        border-color: $bc_blue;
        background-color: $bc_blue;
        &:hover {
            border-color: $bc_blue;
            background-color: $bc_blue;
        }
        &:active {
            border-color: $bc_blue;
            background-color: $bc_blue;
        }
        &.disabled, &:disabled {
            border-color: $bc_blue_disable;
            background-color: $bc_blue_disable;
            opacity: .5;
        }
    }
    /* loading */
    .u-btn .btn_loading {
        margin-top: 6px;
        .i-icon.i-icon-loading {

            -webkit-animation: btnLoadingKF 2s infinite linear;
            animation: btnLoadingKF 2s infinite linear;

            @keyframes btnLoadingKF {
                from {-webkit-transform: rotate(0deg)}
                to {-webkit-transform: rotate(360deg)}
            }

            @-webkit-keyframes btnLoadingKF {
                from {-webkit-transform: rotate(0deg)}
                to {-webkit-transform: rotate(360deg)}
            }

        }
    }

}
.m-ipt{
    .u-btn-sms{
        position: absolute;
        right: 0;
    }
}
