@import "./config";
.m-form{
    .u-mLine{
        display: none;
        height: 1px;
        overflow: hidden;
        padding-left: .4rem;
        background-color:#fff ;
        .line{
            display: block;
            height: 0;
            border-bottom: solid 1px $bd-normal;
        }
    }
    .m-ipt{
        position: relative;
        &.ipt-cap{
            .u-ipt{
                padding-right: 2.5rem;
            }
            &.cap-s{
                border: none;
                background: none ;
            }
        }
        &.ipt-sms{
            .u-ipt{
                padding-right: 2rem;
            }
            .u-btn-sms{
                margin: 0;
                position: absolute;
                top: 0;
                right: 0;
                button{
                    margin: .25rem 0;
                    height: 0.5rem;
                    line-height: .5rem;
                    background: none;
                    border: none;
                    color: $fc_blue;
                    border-left: solid 1px $bd-normal;
                    border-radius:0 ;
                }
                .btn_mask{
                    background-color: #fff;
                }
            }
        }
        &.ipt-pwd{
            .ipt_wrap{
                padding-right: 1.45rem;
            }
            .i-icon-password-clear{
                margin-top: 7px;
                margin-bottom: 3px;
                width: 20px;
            }
        }
    }
    &.iptsBox{
        .m-ipt{
            margin-bottom: 0;
            &.ipt-h{
                border-bottom: none;
            }
            &.ipt-m{
                border-top: none;
                border-bottom: none;
            }
            &.ipt-f{
                border-top: none;
                margin-bottom: .3rem;
            }
        }
        .u-mLine{display: block;}
        &.cap0{
            .ipt-mb{
                border-bottom: none;
            }
            .ipt-cap{
                border-top: none ;
            }
        }
    }

}

.m-ipt{
    background-color: #fff;
    border: solid 1px $bd-normal;
    margin: 0 -1px .3rem;
    .ipt-info{
        min-height: 16px;
        font-size: 12px;
        line-height: 16px;
        padding: 4px 0 ;
        &.if-error{
            color:$fc_err;
        }
    }
    .u-ipt{
        display: block;
        height:auto;
        width: auto;
        .ipt_wrap{
            display: block;
            padding: 0 .8rem 0 .4rem;
            input{
                display: block;
                border: none;
                font-size: 0.28rem;
                height: 1rem;
                width: 100%;
                padding: 0;
                line-height: .28rem;
            }
        }
        .ipt_extend {
            top: .31rem;
            right: .1rem;
            &> i {
                padding: 0;
            }
        }
        .ipt_tip{
            display: none;
        }
    }
}

.m-cap{
    margin-bottom: .3rem;
}

.m-btns{
    margin:.8rem 0 .2rem 0;
    .u-btn{
        display: block;
        button{
            width: 100%;
            font-size: .36rem;
        }
    }
}



