@mixin clearfloat() {
    zoom:1;
    &:after{display:block;clear:both;visibility:hidden;height:0;overflow:hidden;content:".";}
}

$u-input_border : #cccccc;
$u-input_placeholder: #cccccc;
$u-input_error: #d64343;
$u-input_tip: #c4a583;
.u-ipt{
    display: inline-block;
    width: 342px;
    height: 65px;
    position: relative;
    text-align: left;
    .ipt_wrap{
        display: inline-block;
        position: relative;
    }
    .ipt_input{
        width: 328px;
        height: 42px;
        line-height: 42px;
        border: 1px solid $u-input_border;
        border-radius: 2px;
        padding-left: 12px;
        &.ipt_input-error{
            border: 1px solid $u-input_error;
            
        }
    }
    .ipt_input_large{
        width: 328px;
    }
    .ipt_input_small{
        width: 212px;
    }
    // è¾å¥æ¡æ©å±åº
    .ipt_extend{
        position: absolute;
        top: 12px;
        right: 2px;
        background: #fff;
        >i{
            margin-right:12px;
            cursor: pointer;
        }
    }
    .ipt_tip{
        font-size: 12px;
        line-height: 22px;
        min-height: 24px;
        text-align: left;
        color: $u-input_tip;
        word-wrap:break-word; word-break:break-all;
    }
    // è¾å¥æ¡ä¸æ¹éè¯¯æç¤º
    .ipt_tip-error{
        color: $u-input_error;
    }
    .placeholder{
        display: inline-block;
        position: absolute;
        top: 12px;
        left:13px;
        cursor: text;
        font-size: 14px;
        color: $u-input_placeholder;
    }
    @include clearfloat();
}




