@import '../../scss/config';

.m-confirm{
    /* é®ç½©å¼¹çª */
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 101;
    .m-mask {
        width: 100%;
        height: 100%;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 991;
        background-color:#000;
        background-color: rgba(0, 0, 0, .5);
    }
    .m-pop{
        width:75%;height:auto;position:relative;
        margin:0 auto;
        top:50%;
        margin-top:-1.78rem;
        max-width: 6rem;
        border-radius:10px;
        background-color:#fcfcfc;
        z-index: 992;
        .pop-info{
            display:block;
            padding:1rem .5rem 2.1rem .5rem;
            font-size: .36rem;
            line-height:1.3em;
            -webkit-user-select:initial;
            user-select:initial;
            word-break: break-all;
            &>span{
                display: table;
                margin: 0 auto;
            }
        }
        .pop-btn{
            position:absolute;
            display:block;
            width:100%;
            height: 1.2rem;
            bottom:0;
            text-align:center;
            line-height: 1.2rem;
            font-size: .36rem;
            border-top:solid 1px $bd-normal;
            color:$fc_blue;
            cursor: pointer;
        }
        .pop-btn.left{
            left:0;
            width:50%;
            color:#666;
            border-right:solid 1px $bd-normal;
        }
        .pop-btn.right{
            right:0;
            width:50%;
        }
    }


    .confirm-ant-show-mask{
        opacity:1 !important;
        -webkit-animation: confirmShowMaskKF .4s ease;
        animation: confirmShowMaskKF .4s ease;
    }
    /* åå³æ¸å¥å¨ç» */
    @keyframes confirmShowMaskKF{
        0%{opacity:0;}
        100%{opacity:1;}
    }
    @-webkit-keyframes confirmShowMaskKF{
        0%{opacity:0;}
        100%{opacity:1;}
    }
    .confirm-ant-show-pop{
        opacity:1 !important;
        -webkit-animation: confirmShowPopKF .3s ease;
        animation: confirmShowPopKF .3s ease;
    }
    /* åå³æ¸å¥å¨ç» */
    @keyframes confirmShowPopKF{
        0%{opacity:0;transform:scale(1.15,1.15)}
    }
    @-webkit-keyframes confirmShowPopKF{
        0%{opacity:0;-webkit-transform:scale(1.15,1.15)}
    }
    .confirm-ant-hide{
        opacity:0 !important;
        -webkit-animation: confirmHideKF .3s ease;
        animation: confirmHideKF .3s ease;
    }
    /* åå³æ¸å¥å¨ç» */
    @keyframes confirmHideKF{
        0%{opacity:1;}
        100%{opacity:0;}
    }
    @-webkit-keyframes confirmHideKF{
        0%{opacity:1;}
        100%{opacity:0;}
    }
}



