/* loading */
.g-loading{
    display: block;
    position: absolute;
    top: 0;
    z-index: 1999;
    height: 100%;
    width: 100%;
    background-color: $bg_page;
    .m-loading1{
        position: fixed;
        top: 1rem;
        left: 50%;
        margin-left: -25px;
    }
    .u-error-flag{
        display: block;
        margin: 40px auto 20px;
    }
    .u-error-info{
        text-align: center;
        font-size: 18px;
        margin: 20px 0 40px 0;
    }
    &>.u-btn {
        width:100%;
        button {
            display: block;
            width: 85%;
            margin: 0 auto;
        }
    }
    &.loading-closing{
        opacity:0 !important;
        -webkit-animation: loadingHideKF .6s linear;
        animation: loadingHideKF .6s linear;
    }
    @keyframes loadingHideKF{
        0%{opacity:1;}
        100%{opacity:0;}
    }
    @-webkit-keyframes loadingHideKF{
        0%{opacity:1;}
        100%{opacity:0;}
    }
}

