$bg_blue : #2c82ff; //å¤§èæ¯è²

.m-loading0{
    .load-container{
        position: relative;
        width: 80px;
        height: 80px;
    }
    .load-container .loader {
        font-size:12px;
        position: absolute;
        top: 50%;
        left: 50%;
        width:8px;
        height:8px;
        margin:-4px 0 0 -4px;
        border-radius: 50%;
        text-indent: -9999em;
        -webkit-animation: loadingKF 1s infinite linear;
        animation: loadingKF 1s infinite linear;
    }
    .loader:after {
        content: '';
        position: absolute;
        top: 0;
    }
    @-webkit-keyframes loadingKF {
        0%,
        100% {
            box-shadow: 0em -2em 0 0.2em $bg_blue, 1.42em -1.42em 0 0em $bg_blue,     2em 0em 0 0em $bg_blue,   1.42em 1.42em 0 0em $bg_blue,   0em 2em 0 0em $bg_blue,   -1.42em 1.42em 0 0em $bg_blue,  -2em 0em 0 0em $bg_blue,  -1.42em -1.42em 0 0em $bg_blue;
        }
        12.5% {
            box-shadow: 0em -2em 0em 0em $bg_blue,   1.42em -1.42em 0 0.2em $bg_blue,   2em 0em 0 0em $bg_blue,      1.42em 1.42em 0 0em $bg_blue,   0em 2em 0 0em $bg_blue,   -1.42em 1.42em 0 0em $bg_blue,  -2em 0em 0 0em $bg_blue,  -1.42em -1.42em 0 0em $bg_blue;
        }
        25% {
            box-shadow: 0em -2em 0em 0em $bg_blue, 1.42em -1.42em 0 0em $bg_blue,    2em 0em 0 0.2em $bg_blue,    1.42em 1.42em 0 0em $bg_blue,      0em 2em 0 0em $bg_blue,   -1.42em 1.42em 0 0em $bg_blue,  -2em 0em 0 0em $bg_blue,  -1.42em -1.42em 0 0em $bg_blue;
        }
        37.5% {
            box-shadow: 0em -2em 0em 0em $bg_blue, 1.42em -1.42em 0 0em $bg_blue, 2em 0em 0 0em $bg_blue,      1.42em 1.42em 0 0.2em $bg_blue,    0em 2em 0 0em $bg_blue,      -1.42em 1.42em 0 0em $bg_blue,  -2em 0em 0 0em $bg_blue,  -1.42em -1.42em 0 0em $bg_blue;
        }
        50% {
            box-shadow: 0em -2em 0em 0em $bg_blue, 1.42em -1.42em 0 0em $bg_blue, 2em 0em 0 0em $bg_blue,   1.42em 1.42em 0 0em $bg_blue,      0em 2em 0 0.2em $bg_blue,    -1.42em 1.42em 0 0em $bg_blue,     -2em 0em 0 0em $bg_blue,  -1.42em -1.42em 0 0em $bg_blue;
        }
        62.5% {
            box-shadow: 0em -2em 0em 0em $bg_blue, 1.42em -1.42em 0 0em $bg_blue, 2em 0em 0 0em $bg_blue,   1.42em 1.42em 0 0em $bg_blue,   0em 2em 0 0em $bg_blue,      -1.42em 1.42em 0 0.2em $bg_blue,   -2em 0em 0 0em $bg_blue,     -1.42em -1.42em 0 0em $bg_blue;
        }
        75% {
            box-shadow: 0em -2em 0em 0em $bg_blue, 1.42em -1.42em 0 0em $bg_blue, 2em 0em 0 0em $bg_blue,   1.42em 1.42em 0 0em $bg_blue,   0em 2em 0 0em $bg_blue,   -1.42em 1.42em 0 0em $bg_blue,     -2em 0em 0 0.2em $bg_blue,   -1.42em -1.42em 0 0em $bg_blue;
        }
        87.5% {
            box-shadow: 0em -2em 0em 0em $bg_blue,   1.42em -1.42em 0 0em $bg_blue,  2em 0em 0 0em $bg_blue,   1.42em 1.42em 0 0em $bg_blue,   0em 2em 0 0em $bg_blue,   -1.42em 1.42em 0 0em $bg_blue,     -2em 0em 0 0em $bg_blue,     -1.42em -1.42em 0 0.2em $bg_blue;
