
.com-toast{
    display: block;
    position: fixed;
    top: 50%;
    min-height: 42px;
    width: 100%;
    margin-top: -30px;
    z-index: 3999;
    .b-t{
        display: table;
        position: relative;
        margin: 0 auto;
        padding: 13px 25px;
        min-height: 16px;
        min-width: 100px;
        max-width: 60%;
        background: #000;
        background-color: rgba(0,0,0,.7);
        font-size: 14px;
        color: #fff;
        text-align: center;
        border-radius: 2px;
        .i-icon{
            display: block;
            position: absolute;
            width: 16px;
            height: 16px;
            top:14px;
            left: 45px;
        }
        &.warn {
            background-color: #FEFCEE;
            border-color: #F3D995;
            .icon-warn {
                display: block;
            }
            .p-info{
                color:#DF9C1F;
            }
        }
        .p-info{
            display: inline-block;
            line-height: 16px;
        }
    }

    .ant-show{
        opacity:1 !important;
        -webkit-animation: toastShowKF .7s ease-out;
        animation: toastShowKF .7s ease-out;
    }
    /* åå³æ¸å¥å¨ç» */
    @keyframes toastShowKF{
        0%{opacity:0;}
        100%{opacity:1;}
    }
    @-webkit-keyframes toastShowKF{
        0%{opacity:0;}
        100%{opacity:1;}
    }
    &.toast-hiding{
        opacity:0 !important;
        -webkit-animation: toastHideKF .2s linear;
        animation: toastHideKF .2s linear;
    }
    @keyframes toastHideKF{
        0%{opacity:1;}
        100%{opacity:0;}
    }
    @-webkit-keyframes toastHideKF{
        0%{opacity:1;}
        100%{opacity:0;}
    }
}



