$btn-color: #ffffff;
$btn-border: #C4A583;
$btn-background: #C4A583;
$btn-disabled: #ddcdbf;
$btn-sms-border:  #C4A583;
$btn-sms-background: #C4A583;
$btn-sms-color: #ffffff;


.u-btn{
    display: inline-block;
    vertical-align: top;
    position:relative;
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    /*
        Introduced in IE 10.
        See http://ie.microsoft.com/testdrive/HTML5/msUserSelect/
    */
    -ms-user-select: none;
    user-select: none;
    >button{
        width: 340px;
        height: 44px;
        line-height: 44px;
        border: 1px solid $btn-border;
        border-radius: 2px;
        text-align: center;
        font-size: 18px;
        cursor: pointer;
        background-color: $btn-background;
        color: $btn-color;

        &.z-dis{
            background-color: $btn-disabled;
            border: 1px solid $btn-disabled;
            
        }
    }   
    .btn_mask{
        position:absolute;
        width:100%;
        height:100%;
        border-radius: 2px;     
        top:0;
        background:#000;-moz-opacity:0.5;-khtml-opacity:0.5;filter:alpha(opacity = 50);
        opacity:0.5;
    }
    .btn_loading{
        margin-top: 13px;
        text-align: center;
    }
}

