@import '../../scss/config';
.m-mbinput {

    &.mb-itnal {
        .m-i18n-mb {
            width: 100%;
            height: 100%;
            position: fixed;
            top: 0;
            left: 0;
            z-index: 993;
        }
        .ipt_wrap{
            padding-left: 1.3rem;
        }
        /* å½æ */
        .u-ic {
            position: absolute;
            top: 0;
            width: 62px;
            height: 50px;
            z-index: 11;
            text-align: right;
            font-size: 1.5rem;
            line-height: 2rem;
            em {
                position: absolute;
                left: 0.4rem;
                top: 17px;
                display: block;
                height: 16px;
                width: 20px;
                font-size: 16px;
            }
            .btn {
                position: absolute;
                left: 43px;
                top: 20px;
                display: block;
            }
        }
        .m-pop-ic {
            width: 80%;
            max-width: 8rem;
            position: relative;
            margin: 0 auto;
            top: 15%;
            height: 70%;
            border-radius: 4px;
            overflow-y: auto;
            -webkit-overflow-scrolling: touch;
            padding-bottom: 10px;
            background-color: #fff;
            z-index: 12;
            .u-country {
                display: block;
                margin: 0 auto;
                border-radius: 4px;
                i {
                    display: block;
                    height: 42px;
                    line-height: 42px;
                    padding:0 10px;
                    color: #464b4d;
                    overflow: hidden;
                    border-bottom: solid 1px #ddd;
                    &.u-tlt{
                        color: #999;
                        text-indent: 10px;
                    }
                }
            }
            em {
                height: 1em;
                width: 1.25em;
                display: block;
                float: left;
                margin: 11px;
                font-size: 20px;
            }
        }
        em {
            background: url(../../res/img/flags-l.png) no-repeat;
            background-size: 1.25em 212em;
            font-size: 16px;
        }
