            width: 100%;
            height: 100%;
            position: fixed;
            top: 0;
            left: 0;
            z-index: 10;
            background:#999;
            background-color: rgba(0, 0, 0, .5);
        }
        .i18nMb-ant-show-mask{
            opacity:1 !important;
            -webkit-animation: i18nMbShowMaskKF .4s ease;
            animation: i18nMbShowMaskKF .4s ease;
        }
        /* åå³æ¸å¥å¨ç» */
        @keyframes i18nMbShowMaskKF{
            0%{opacity:0;}
            100%{opacity:1;}
        }
        @-webkit-keyframes i18nMbShowMaskKF{
            0%{opacity:0;}
            100%{opacity:1;}
        }
        .i18nMb-ant-show-pop{
            opacity:1 !important;
            -webkit-animation: i18nMbShowPopKF .3s ease;
            animation: i18nMbShowPopKF .3s ease;
        }
        /* åå³æ¸å¥å¨ç» */
        @keyframes i18nMbShowPopKF{
            0%{opacity:0;transform:scale(1.15,1.15)}
        }
        @-webkit-keyframes i18nMbShowPopKF{
            0%{opacity:0;-webkit-transform:scale(1.15,1.15)}
        }
        .i18nMb-ant-hide{
            opacity:0 !important;
            -webkit-animation: i18nMbHideKF .3s ease-out;
            animation: i18nMbHideKF .3s ease-out;
        }
        /* åå³æ¸å¥å¨ç» */
