$umobile-list-border : #C4A583;
$umobile-list-hover : #C4A583;
$umobile-list-sel : #C4A583;
$umobile-list-background : #ffffff;

.u-mbipt{
    .u-country {
        position: absolute;
        height: 46px;
        line-height: 46px;
        padding-left: 12px;
        cursor: pointer;
        z-index: 9;
    }

    .u-country>span>i {
        display: inline-block;
        width: 0px;
        height: 0px;
        position: relative;
        // left: -4px;
        top: 4px;
        border-top: 6px solid #CCC;
        border-bottom: 6px solid white;
        border-left: 6px solid white;
        border-right: 6px solid white;
    }
    .mobile_item{
        >i{
            border:none;
            padding-left:4px;
        }
        &.z-sel{
            background-color:$umobile-list-sel;
        }
    }
    .ipt_input.ipt_input_umobile{
        width: 290px;
        padding-left: 50px;
    }
    .u-mobile-list{
        position: absolute;
        left: 0;
        top: 44px;
        z-index: 11;
        width:340px;
        height:183px;
        overflow-y: scroll;
        border:1px solid $umobile-list-border; 
        text-align:left;
        background-color: $umobile-list-background;
        >li{
            display: block;
            height: 30px;
            line-height: 30px;
            padding-left:12px;
            &:hover{background-color:$umobile-list-hover;}
        }
        
    }
    .mobile_tlt{
        padding-left:12px;
        color:#666666;
        height:30px;
        line-height:30px;
    }
    .placeholder{
        
        left:51px;
        
    }
